@use "hero";
@use "./components/infos_bar";
@use "./components/buttons";
@use "./components/form";
@use "event";
@use "variables";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  color: variables.$textColor;
  list-style: none;
}

body {
  background-color: #06692f;
}

.container {
  max-width: 960px;
  width: 100%;
  margin: 0 auto;

  @media (max-width: 640px) {
    max-width: 80%;
  }

  @media (max-width: 1024px) {
    max-width: 90%;
  }
}
